@import '../utility/variables';

ul,
li {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4 {
  margin: 0;
  padding: 0;
}
body {
  background-color: $bcg-pink-light;
}
@keyframes change-icon-size {
  0% {
    transform: scale(1);
  }
  // 50% {
  //   transform: scale(1.1);
  // }
  100% {
    transform: scale(1.1);
  }
}
.advantages__title {
  margin-bottom: 15px;
  transform: scale(1);
  transform-origin: 0 50%;
  transition: transform 250ms linear;
}
.advantages__title:hover {
  transform: scale(1.2);
  transform-origin: 0 50%;
  // animation-name: change-icon-size;
  // animation-duration: 1000ms;
  // animation-timing-function: linear;
  // animation-iteration-count: 1;
  // animation-direction:normal;
}
@media (max-width: 767px) {
  .advantages {
    max-width: 320px;
    @media (min-width: 480px) {
      margin-right: auto;
      margin-left: auto;
    }
    margin-left: 20px;
    margin-right: auto;
    margin-bottom: 70px;
    max-width: 320px;
    margin-top: 152px;
    &__item {
      position: relative;

      margin-top: 110px;

      width: 189px;
    }
    &__title {
      font-family: $fontTitanOne;
      font-size: 36px;
      line-height: 41px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $accent-color;
    }
    &__text {
      font-family: $fontDMSans;
      font-weight: 700;
      font-size: 12px;
      line-height: 20px;
      color: $text-secondary;
    }
  }
}

@media (min-width: 768px) {
  .advantages-section {
    // margin-top: 60px;
  }
  .advantages {
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    // margin-top: 152px;
    // margin-bottom: 70px;
    padding: 70px 30px;

    display: flex;
    &__item {
      position: relative;
      //   margin-top: 90px;
      width: calc((100% - 160px) / 3);
      &:not(:last-child) {
        margin-right: 80px;
      }
    }
    &__title {
      font-family: $fontTitanOne;
      font-size: 48px;
      line-height: 55px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $accent-color;
    }
    &__text {
      font-family: $fontDMSans;
      font-weight: 700;
      font-size: 14px;
      line-height: 24px;
      color: $text-secondary;
    }
  }
}
@media (min-width: 1280px) {
  .advantages {
    display: flex;
    justify-content: center;
    max-width: 1000px;
    &__item {
      position: relative;
      width: calc((100% - 180px) / 3);
      max-width: 270px;
      &:not(:last-child) {
        margin-right: 90px;
      }
    }
    &__title {
      font-family: $fontTitanOne;
      font-size: 58px;
      line-height: 66px;
      letter-spacing: 0.04em;
      text-transform: uppercase;
      color: $accent-color;
    }
    &__text {
      font-family: $fontDMSans;
      font-weight: 700;
      font-size: 16px;
      line-height: 28px;
      letter-spacing: 0.02em;
      color: $text-secondary;
    }
  }
}
@media (min-width: 768px) {
}
.one::before {
  position: absolute;
  content: '';
  width: 50px;
  height: 50px;
  // top: auto;
  top: 0px;
  @media (max-width: 767px) {
    top: -80px;
  }
  @media (min-width: 768px) {
    top: -70px;
  }

  background-image: url('../images/advantage-one-1x.png');
  background-repeat: no-repeat;
}
@media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
  .one::before {
    background-image: url('../images/advantage-one-2x.png');
    background-repeat: no-repeat;
    background-size: contain;
    width: 50px;
    height: 50px;
  }
}

